// countriesLocations{Usa:[]...}
export const getCountriesLocations = (locations) => {
    console.log("getCountriesLocations" , locations)
    const countriesLocations = {}
    locations.options.forEach(location => {
        if (location.country !== null) {
            if(countriesLocations[location.country] && countriesLocations[location.country]
                && (countriesLocations[location.country]) !== undefined){
                countriesLocations[location.country].push(location)
            }else{
                countriesLocations[location.country] = []
                countriesLocations[location.country].push(location)
            }
        }
    })
    return countriesLocations
}

// []
export const getCityLocations = (locations, country, city) => {
    console.log("getCityLocations" , locations)
    return locations.options.filter(location => (location.country === country && location.city === city))
}

//Это для вывода всего локейшена allLocationsAssembledByCity{cityname: []}

export const getCountryNodes = (locations) => {
    console.log("getCountryNodes" , locations)
    const getSortedLocationsByCountry = getCountriesLocations(locations)
    const allLocationsAssembledByCity = {}
    console.log("#9985", Object.keys(getSortedLocationsByCountry))
    Object.keys(getSortedLocationsByCountry).forEach(key => {
        getSortedLocationsByCountry[key].forEach(city => {
            if( allLocationsAssembledByCity[city.city] &&  allLocationsAssembledByCity[city.city] !==null &&
                allLocationsAssembledByCity[city.city]!== undefined) {
                allLocationsAssembledByCity[city.city].push(city)
            }else {
                allLocationsAssembledByCity[city.city] = []
                allLocationsAssembledByCity[city.city].push(city)
            }
        })
    })
    return allLocationsAssembledByCity
}


//[]
export const geFilteredCountryNodes = (locations, country) => {
    console.log("geFilteredCountryNodes" , locations)
    return locations.options.filter(location => location.country === country)
}

//сда приходят одной страны локации а должны выйти локации разделенные по городам
export const getLocationsFilteredByCity = (locations, city) => {
    return locations.filter(location => location.city === city)

}
export const getCityLocationsSortedByCity = (locations) => {
    const citiesAndNodes = {}
    locations.forEach(location => {
        if(citiesAndNodes[location.city] &&
            citiesAndNodes[location.city] !== null &&
            citiesAndNodes[location.city] !== undefined){
            citiesAndNodes[location.city].push(location)
        }else{
            citiesAndNodes[location.city] = []
            citiesAndNodes[location.city].push(location)
        }
    })
    return citiesAndNodes
}