import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {changeUserCity, changeUserCountry} from "../../../redux-state/reducers/locationSlicer";

const BreadCrumbsContainer = (props) => {
    const dispatch = useDispatch()
    const currentCountry = useSelector((state)=>state.location.user_country)
    const returnButtonHandler=()=>{
        dispatch(changeUserCountry(null))
    }
    return (
        <ul className="select-subgroup__breadcrumbs">
            <li className="return" title="Вернуться к списку стран" onClick={returnButtonHandler}>
                <i className="fa fa-lg fa-reply" ></i>
            </li>
            <li className="label active">
                {currentCountry}
            </li>
        </ul>
    )
}

export default BreadCrumbsContainer