import React from "react";
import {useSelector, useDispatch} from "react-redux";
import {changeUserSoftware, changeUserOs, changeUserClickSoftListExpand} from "../redux-state/reducers/softWareSlicer";
const SoftListItem = (props) => {
    console.log("#8542", props)
    const dispatch = useDispatch()
    const dataType = props['data-type']
    const softObject = props['data-object']
    const setUserSoft = () => {
        if(dataType === "os"){
            dispatch(changeUserOs(softObject))
        }else{
            dispatch(changeUserSoftware(softObject))
        }
        dispatch(changeUserClickSoftListExpand(null))
    }
    return (
        <div className="select-group__option" key={props['data-key']} onClick={setUserSoft}>{props['data-name']}
        </div>
    )
}
export default SoftListItem