import React from "react";
import {useSelector} from "react-redux";
import {getSoftwareNAme} from "../../common/rigthSideContainerFunctions";

export const SoftWareSection = (props) => {
    const configurator = props['datta-configurator']
    const software = useSelector(state => state.software.software)
    const os = useSelector(state => state.software.user_os)
    const soft = useSelector(state => state.software.user_software)
    const softWareName = getSoftwareNAme(configurator, software, os, soft)

    return (
        <tr>
            <td className="label">
                <span className="configurator-item-label__software">{softWareName}</span></td>
            <td className="value" nowrap="">
                                        <span className="configurator-price configurator-price__software"
                                              data-bind_currency="true">0 ₽</span> <span
                className="configurator-value__software_info"></span></td>
        </tr>
    )
}

