import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    user_country: null,
    user_city: null,
    user_node: null,
    view_type: "nesting", //или nesting
    node_list_expand: null,
}

export const changeLocationReducer = createSlice(
    {
        name: 'locationChanger',
        initialState,
        reducers: {
            changeUserCountry: (state, action) => {
                state.user_country = action.payload
            },
            changeUserCity: (state, action) => {
                state.user_city = action.payload
            },
            changeUserNode: (state, action) => {
                state.user_node = action.payload
            },
            changeViewType: (state, action) => {
                state.view_type = action.payload
            },
            changeNodeListExpand: (state, action) => {
                state.node_list_expand = action.payload
            }
        }
    }
)

export const {
    changeNodeListExpand,
    changeUserCity,
    changeUserCountry,
    changeUserNode,
    changeViewType
} = changeLocationReducer.actions
export default changeLocationReducer.reducer