import React from "react";
import {useSelector} from "react-redux";

export const RamSize = (props) => {
    const ram = useSelector(state => state.slider.ram)
    return (
        <tr>
            <td className="label">
                Объем RAM
            </td>
            <td className="value" nowrap="">
                <span className="configurator-item-value__ram">{ram}</span> Mb
            </td>
        </tr>
    )
}

