import React from "react";
import {useDispatch} from "react-redux";
import {changeViewType} from "../../../redux-state/reducers/locationSlicer";

const ViewTypeContainer = (props) => {
    const dispatch = useDispatch()
    const setViewType = (newViewType) => {
        console.log(newViewType)
        dispatch(changeViewType(newViewType))
    }
    return (
        <div className="subgroups-style">
            <i className={props['data-view'] === 'nesting' ? "active fa fa-lg fa-th-large" : "fa fa-lg fa-th-large"} data-style="th" onClick={()=>setViewType('nesting')}></i>
            <i className={props['data-view'] ===  'all' ? "active fa fa-lg fa-th" : "fa fa-lg fa-th"} data-style="list" onClick={() => setViewType("all")}></i>
        </div>
    )
}

export default ViewTypeContainer