import React from "react";
import {getSoftForList} from "../../common/softGroupFunctions";
import SoftListItem from "../SoftListItem";
const SoftList = (props) => {
    const softList = getSoftForList(props['data-used-version'], props['data-all-versions'])
    const sortedSoftListItems = []
    const dataType = props['data-type']
    const expandList = props['data-expand']
    console.log('softlist',softList)
    softList.forEach(item => {
        sortedSoftListItems.push(<SoftListItem key={item.uid} data-name={dataType === 'os' ? item.name : item.os.name}
                                               data-type={dataType} data-object={item}/>)
    })
    return (

            <div className={expandList === true ? "select-group__options active" : "select-group__options"}>
                <div className="select-group__options-content">
                    {sortedSoftListItems}
                </div>
            </div>
    )
}
export default SoftList