export const getHddTech = (hddTechIndex, configurator) => {
    console.log("HDDTECHINDEX", hddTechIndex)
    return configurator.storage.options[hddTechIndex ? hddTechIndex : 0].alternative_name
}
export const getSpeedHdd = (hddTechIndex, configurator) => {
    console.log("IO GET", hddTechIndex)
    return configurator.storage.options[hddTechIndex ? hddTechIndex : 0].io
}
export const getSoftwareNAme = (configurator, software,  os, soft) => {
    switch (software){
        case 'os':
            // return os.name
            return "Ocm"
        case 'software':
        // return soft.name
            return "Софт"
        default:
            return "Не установлен"
    }
}