import {sliderSlicersActions, slidersStyle, sliderStylesToConfigurator} from "./constants";

// {data_slider:[storage,...], zoom_slider:[cpu,...]}
export const getSlidersData = (configurator) => {
    const slidersData = {data_slider: [], zoom_slider: []}
    Object.keys(sliderStylesToConfigurator).forEach(key => {
        const typeOfSliderStyle = sliderStylesToConfigurator[key]
        switch (typeOfSliderStyle) {
            case slidersStyle.ZOOM_SLIDER:
                configurator[key].guid = key
                slidersData.zoom_slider.push(configurator[key])
                break
            case slidersStyle.SLIDER:
                configurator[key].guid = key
                slidersData.data_slider.push(configurator[key])
                break
        }
    })
    return slidersData
}

export const getTicksLabels = (options) => {
    const ticksLabels = []
    options.forEach(option => {
        ticksLabels.push(option.name)
    })
    return ticksLabels
}

export const getReducer = (guid) => {
    console.log("getReducer in harware", guid)
    return sliderSlicersActions[guid]
}

export const getDefaultValue = (sliderData) => {
    console.log("DEFF VAL ", sliderData)
    return sliderData.value
}

export const getDefaultValueForDataSlider = (data) => {
    const defaultValue = data.value
    let indexOfValue = null
    data.options.map((value, index) => {
            if (value.uid == defaultValue) {
                indexOfValue = index
            }
        }
    )
    return indexOfValue
}