import React from "react";
const ListNodesCityButton = (props) => {
    const node = props['data-location']
    const setNewUserCityAndNode = props['data-onclick']
    return (
        // active тут добавляется если список раскрыт и arrow active
        <div className="select-group__option" onClick={()=>setNewUserCityAndNode(node)}>
            {node.name}
        </div>
    )
}

export default ListNodesCityButton
