import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {changeCheckBox} from "../redux-state/reducers/rightSectionSlicer";

const OrderButtonAndPrivacyCheckboxContainer = () => {
    const checkBoxState = useSelector(state => state.calculator.check_box)
    const dispatch = useDispatch()
    const setCheckBox = (value) => {
        console.log("CheckboxChange" , value)
        dispatch(changeCheckBox(value.target.checked))
    }
    return (
        <>
            <div className="order_btn">
                <div className="configurator-loader loader" style={{display: 'none'}}></div>
                <div className="info-box" style={{fontSize: '13PX'}}>
                    <input type="checkbox"  onChange={setCheckBox} className="order-accept-terms" checked={checkBoxState}/>
                    Я принимаю&nbsp;<a href={"https://kb.justhost.ru/docs/"} target="_blank">условия оферты <img
                    src={"https://justhost.ru/assets/themes/standart/img/site/ico_external.gif"} alt=""/></a>
                </div>
                <div className="order__doorder" data-action="doOrder" data-link="/api/v1/vps/order"
                     aria-disabled={!checkBoxState}>Заказать
                </div>
            </div>
        </>
    )
}

export default OrderButtonAndPrivacyCheckboxContainer