import {configureStore} from "@reduxjs/toolkit";
import softWareReducer from './reducers/softWareSlicer'
import locationReducer from "./reducers/locationSlicer";
import sliderReducer from "./reducers/sliderSlicer";
import RightSectionReducer from "./reducers/rightSectionSlicer";

export const store = configureStore({
    reducer: {
        software: softWareReducer,
        location: locationReducer,
        slider: sliderReducer,
        calculator: RightSectionReducer,
    },
    devTools: true, // Включение DevTools только в режиме разработки
})