import React from "react";
import {useSelector} from "react-redux";
import BreadCrumbsContainer from "./elements/BreadCrumbsContainer";
import ViewTypeContainer from "./elements/ViewTypeContainer";
import CityButton from "./elements/CityButton";
import {getCityLocationsSortedByCity} from "../../common/locationsFunctions";

const CityContainer = (props) => {
    const locations = props['data-locations']
    const viewType = props['data-viewType']
    console.log("CityContainer", locations)
    console.log("CityContainer", viewType)
    const citiesAndNodes = getCityLocationsSortedByCity(locations)
    console.log("citiesAndNodes", citiesAndNodes)
    const getCityButtons = () => {
        const cityButtons = []
        Object.keys(citiesAndNodes).forEach(key => {
            console.log("Key", key)
            cityButtons.push(<CityButton data-locations={citiesAndNodes[key]} key={key}/>)
        })
        return cityButtons
    }
    const buttons = getCityButtons()

    return (
        <div className="subgroups active">
            <ViewTypeContainer data-view={viewType}/>
            <BreadCrumbsContainer/>
            <div className="group active" data-key="subgroup_0">
                <div className="select-groups select-groups-location" data-name="location">
                {buttons}
                </div>
            </div>
        </div>
    )
}

export default CityContainer