import React from "react";
import ReactBootstrapSlider from "react-bootstrap-slider/dist/react-bootstrap-slider";
import {useDispatch, useSelector} from "react-redux";
import {getDefaultValue} from "../../../common/hardwareFunctions";

const SliderZoom = (props) => {
    const dispatch = useDispatch()
    const sliderReducer = props['data-reducer']

    const slideStop = (value) => {
        dispatch(sliderReducer(value.target.value))
        console.log("SLIDE STOP", value)
    }
    const dataOfSlider = props['data-slider']
    const currentValueFromState = useSelector(state => state.slider[dataOfSlider.guid])
    console.log("currentValueFromState", currentValueFromState)
    console.log("SLIDR ZOOM ", dataOfSlider)

    const changeValue = (value) => {
        console.log("Change value", value)
        dispatch(sliderReducer(value.target.value))
    }
    const getCurrentSliderValue = () => {
        if (currentValueFromState) {
            return currentValueFromState
        } else {
            return getDefaultValue(dataOfSlider)
        }
    }

    const validateInput = (value) => {
        console.log("VALUE IN VALIDATOR " , value, dataOfSlider.range.max, dataOfSlider.range.min)
        if (dataOfSlider.range.max >= value && value >= dataOfSlider.range.min){
            return true
        }
        return false
    }
    const inputValueChange = (value) => {
        console.log("changeValue from input", value)
        if (validateInput(value.target.value)) {
            console.log("changeValue from input Validate +", value)
            dispatch(sliderReducer(value.target.value))
        } else {
            console.log("changeValue from input Validate -", value)
            const defVal = getDefaultValue(dataOfSlider)
            // dispatch(sliderReducer(defVal))
        }

    }

    const sliderCurrentValue = getCurrentSliderValue()
    return (
        <div className="slider-wrapper  slider-zoom" data-slider-name={dataOfSlider.guid}>
            <div className="labels">
                <label htmlFor={dataOfSlider.guid}>{dataOfSlider.name}</label>
                <input type="number" onChange={inputValueChange} name={dataOfSlider.guid}
                       value={sliderCurrentValue} data-gtm-form-interact-field-id="0"
                />
            </div>

            <ReactBootstrapSlider
                value={sliderCurrentValue}
                change={changeValue}
                slideStop={slideStop}
                step={dataOfSlider.range.step}
                max={dataOfSlider.range.max}
                min={dataOfSlider.range.min}
                orientation={"horizontal"}
                reversed={false}
                disabled={"no"}
                handle={"round"}
                tooltip={"hide"}
            />
            <div className="slider-bottom">
                <div className="slider-min-value">{dataOfSlider.range.min}</div>
                <div className="slider-max-value">{dataOfSlider.range.max}</div>
            </div>
        </div>
    )
}

export default SliderZoom