import React from "react";
import SoftSelector from "./SoftSelector";
import SoftItem from "./SoftItem";
import {getListsOfSoftWare, getActiveGroupButton} from "../../common/softGroupFunctions";
import { useSelector, useDispatch } from 'react-redux'
import {changeSoftware} from '../../redux-state/reducers/softWareSlicer'

const SoftGroup = (props) => {
    const softWares = getListsOfSoftWare(props.software.options)
    const stateSoftWare = useSelector((state) => state.software)
    const activeGroupButton = getActiveGroupButton(stateSoftWare, props.software)
    const actualOsList = activeGroupButton.osButton === true ? softWares['os'] : softWares['software']
    const dispatch = useDispatch()
    const getSoftTypeKey = () => {
        if (activeGroupButton.osButton === true) {
            return 'os'
        }
        return 'soft'
    }

    return (
        <div className={"groups groups-software 1"}>
            <div className={"labels"}>
                <SoftSelector active-button={activeGroupButton} />
            </div>
            <div className={"group active"}>
                <div className={"select-groups select-groups-software"}>
            { Object.keys(actualOsList).map((softwareName, index) => {
                return <SoftItem key={index} data-soft={actualOsList[softwareName]} data-key={softwareName} data-type={getSoftTypeKey()}/>
            })}
            </div>
            <div className={"enter-message"} onClick={() => dispatch(changeSoftware("Ykkvkkf"))}>После создания VPS в личном кабинете вы сможете подключить свой ISO образ и
                установить любую вашу ОС.
            </div>
            </div>
        </div>)
}

export default SoftGroup