import React from "react";
import SliderZoom from "./elements/SliderZoom";
import DataSlider from "./elements/DataSlider";
import {getSlidersData, getReducer} from "../../common/hardwareFunctions";

const SliderContainer = (props) => {
    const configurator = props['configurator']
    const slidersData = getSlidersData(configurator)
    console.log("SLIDERSDATA", slidersData)
    const getSliders = () => {
        const allSliders = []
        slidersData.zoom_slider.forEach(slider => {
            allSliders.push(<SliderZoom data-slider={slider} data-reducer={getReducer(slider.guid)}/>)
        })
        slidersData.data_slider.forEach(slidersData => {
            allSliders.push(<DataSlider data-slider={slidersData} data-reducer={getReducer(slidersData.guid)}/>)
        })
        return allSliders
    }
    return (
        <div>
        {getSliders()}
        </div>
    )
}

export default SliderContainer