import React from "react";
import LookingGlassButton from "./elements/LookingGlassButton";
import ViewTypeContainer from "./elements/ViewTypeContainer";
import BreadCrumbsContainer from "./elements/BreadCrumbsContainer";
import CityContainer from "./CityContainer";
import CountryContainer from "./CountryContainer";
import {useSelector} from "react-redux";
import {
    geFilteredCountryNodes,
    getCityLocations,
    getCountriesLocations,
    getCountryNodes
} from "../../common/locationsFunctions";
import AllLocationsContainer from "./AllLocationsContainer";

const LocationsContainer = (props) => {
    const viewType = useSelector((state) => state.location.view_type)
    const userCountry = useSelector((state) => state.location.user_country)
    const locations = props.locations
    const getLocationsBlock = () => {
        switch (viewType){
            case 'all':
                const countryNodesByCity = getCountryNodes(locations)
                return <AllLocationsContainer data-viewType={viewType} data-locations={countryNodesByCity}/>
            case 'nesting':
                const nestingLocations = userCountry === null ? getCountriesLocations(locations) : geFilteredCountryNodes(locations, userCountry)
                return userCountry === null ? <CountryContainer data-locations={nestingLocations} data-viewType={viewType}/> : <CityContainer data-locations={nestingLocations} data-viewType={viewType}/>
        }
    }
    const locationsBLock = getLocationsBlock()
    return (
        <div className={"groups groups-location 1"}>
            <div className={"labels"}>
                <label className={"active"}> Размещение</label>
            </div>
            <div className={"group active"}>

                    {locationsBLock}
            </div>
            <LookingGlassButton/>
        </div>)
}

export default LocationsContainer