import React from "react";
import {useDispatch } from 'react-redux'
import {changeSoftware} from '../../redux-state/reducers/softWareSlicer'
const SoftSelectorSoftButton = (props) => {
    const dispatch = useDispatch()
    const changeStateSoftware = (elem) => {
        console.log(elem)
        dispatch(changeSoftware(elem))
    }
    const styleClass =  props['activeButton']['active-button']['softButton'] === true ? "group active" : "group"
    return (
        <label className={styleClass}  onClick={() => changeStateSoftware("software")} >
            {props.text}
        </label>
    )
}

export default SoftSelectorSoftButton