import React from "react";
import {useSelector} from "react-redux";
import {getHddTech} from "../../common/rigthSideContainerFunctions";

export const RomCapacity = (props) => {
    const romSize = useSelector(state => state.slider.storage_size)
    const hddTechIndex = useSelector(state => state.slider.storage)
    const configurator = props['data-configurator']
    const romTech = getHddTech(hddTechIndex,configurator)
    return (
        <tr>
            <td className="label">
                Объем <span className="configurator-item-value__storage">{romTech}</span></td>
            <td className="value" nowrap="">
                <span className="configurator-item-value__storage_size">{romSize}</span> Gb
            </td>
        </tr>
    )
}

