import React from "react";
import {useSelector} from "react-redux";

export const LocationSection = (props) => {
    const location = useSelector(state => state.location.user_node)
    return (

        <tr>
            <td className="label">
                Размещение
            </td>
            <td className="value">
                <span className="configurator-item-label__location">{location ? location.name : "Неопределено"}</span>
            </td>
        </tr>
    )
}

