import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {
    changeNodeListExpand,
    changeUserCity,
    changeUserCountry,
    changeUserNode
} from "../../../redux-state/reducers/locationSlicer";


const AutoButtonLocation = () => {
    const dispatch = useDispatch()
    const userNode = useSelector(state => state.location.user_node)
    const autoLocation = {
        uid: "auto",
        name: "Auto",
        country: null,
        city: null,
        image: null
    }
    const setAutoLocation = () => {
        dispatch(changeUserNode(autoLocation))
        dispatch(changeNodeListExpand(null))
        dispatch(changeUserCountry(null))
        dispatch(changeUserCity(null))
    }
    const getClassOfButton = () => {
        if(userNode && userNode.uid === 'auto'){
            return ' active'
        }
        return ''
    }
    return (
        <div className={"select-group select-group-auto    select-group__hide-selector " + getClassOfButton()} data-value="auto">
            <div className="select-group__content select-group__content-auto" onClick={setAutoLocation}>

                <div className="select-group__header">
                    Auto
                </div>

                <div className="select-group__selector disabled">
                    <div className="select-group__selected"></div>
                    <div className="arrow">
                        <div className="arrow-left"></div>
                        <div className="arrow-right"></div>
                    </div>
                    <div className="select-group__options" data-name="auto">
                        <div className="select-group__options-content">
                            <div className="select-group__option active" data-value="auto" data-default="false">Auto
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default AutoButtonLocation