import React from "react";
import {useSelector} from "react-redux";
import {getHddTech, getSpeedHdd} from "../../common/rigthSideContainerFunctions";

export const RomTypeAndBandwigth = (props) => {
    const hddTechIndex = useSelector(state => state.slider.storage)
    const configurator = props['data-configurator']
    const hddTech = getHddTech(hddTechIndex, configurator)
    const speed = getSpeedHdd(hddTechIndex,configurator)
    return (
        <tr>
            <td className="label">
                <span className="configurator-item-value__storage">{hddTech}</span> I/O
            </td>
            <td className="value">
            <span style={{display: 'inline'}}>{speed} MB/sec</span>
            </td>
        </tr>

    )
}

