import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    cpu: null,
    ram: null,
    storage_size: null,
    netbandmb: null,
    ipv4_count: null,
    ipv6_count: null,
    svc_count: null,
    payment_months: null,
    storage: null,
    cpu_options: null,

}

export const changeSliderReducer = createSlice(
    {
        name: 'sliderChanger',
        initialState,
        reducers: {
            changeCpu: (state, action) => {
                state.cpu = action.payload
            },
            changeRam: (state, action) => {
                state.ram = action.payload
            },
            changeStorageSize: (state, action) => {
                state.storage_size = action.payload
            },
            changeNetBandMB: (state, action) => {
                state.netbandmb = action.payload
            },
            changeIpV4: (state, action) => {
                state.ipv4_count = action.payload
            },
            changeIpV6: (state, action) => {
                state.ipv6_count = action.payload
            },
            changeSvcCount: (state, action) => {
                state.svc_count = action.payload
            },
            changePaymentMonths: (state, action) => {
                state.payment_months = action.payload
            },
            changeStorage: (state, action) => {
                state.storage = action.payload
            },
            changeCpuOptions: (state, action) => {
                state.cpu_options = action.payload
            },
        }
    }
)

export const {
    changeCpu,
    changeCpuOptions,
    changeIpV4,
    changeIpV6,
    changeNetBandMB,
    changePaymentMonths,
    changeRam,
    changeStorage,
    changeStorageSize,
    changeSvcCount,

} = changeSliderReducer.actions
export default changeSliderReducer.reducer