import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    user_os: null, //ручной выбор пользователем в разделе осей
    user_software: null, //ручной выбор пользователем в разделе предустановленного по
    software: null, //тут значение типа выбранной вкладки os или software
    user_soft_list_click_expand: null,
}

export const changeSoftwareReducer = createSlice(
    {
        name: 'softwareChanger',
        initialState,
        reducers: {
            changeSoftware: (state, action) => {
                state.software = action.payload
            },
            changeUserOs: (state, action)=>{
                state.user_os = action.payload
            },
            changeUserSoftware: (state, action) => {
                state.user_software = action.payload
            },
            changeUserClickSoftListExpand: (state, action) => {
                state.user_soft_list_click_expand = action.payload === state.user_soft_list_click_expand ? null : action.payload
}
        }
    }
)

export const {changeSoftware, changeUserOs, changeUserSoftware, changeUserClickSoftListExpand} = changeSoftwareReducer.actions
export default changeSoftwareReducer.reducer