import React from "react";
import {useDispatch} from "react-redux";
import {changeUserCountry} from "../../../redux-state/reducers/locationSlicer";

const CountryButton = (props) => {
    const dispatch = useDispatch()
    const country = props['data-country']
    const stringCountryInLowerCase = country.toLowerCase()
    const setCountryStateHandler = () => dispatch(changeUserCountry(country))
    return (
        <div className={"select-subgroup select-subgroup-" + stringCountryInLowerCase} onClick={setCountryStateHandler} data-subgroup={stringCountryInLowerCase}>
            <div className={"select-subgroup___content select-subgroup__content-" + stringCountryInLowerCase +  " geo geo-"+ stringCountryInLowerCase}>
                <div className="select-subgroup__header">
                    {country}
                </div>
            </div>
        </div>
    )
}

export default CountryButton