import './App.css';
import SoftGroup from "./components/software/SoftGroup";
import './styles/jhstyles.css'
import json from './datas/vps.json'
import LocationsContainer from "./components/locations/LocationsContainer";
import SliderContainer from "./components/hardware/SliderContainer";
import OrderCommentContainer from "./components/OrderCommentContainer";
import OrderButtonAndPrivacyCheckboxContainer from "./components/OrderButtonAndPrivacyCheckboxContainer";
import RightSideContainer from "./components/rigth_sight/RightSideContainer";


function App() {
    console.log(json)
    const softWares = () => {
        return json.result.configurator.software
    }
    const locations = () => {
        return json.result.configurator.locations
    }
    const configurator = () => {
        return json.result.configurator
    }
    return (
        <>
            <div className={"vps_config_default"}>
                <div className={"lnd__col1"}>
                    <div className={"lnd__features-cont"}>
                <span className={"lnd__details-desc"}>Конфигурацию можно менять в любой момент после заказа сервера.
                </span>
                        <ul>
                            <li className={"first"}>
                                <div id={"vps-params-plate"}>
                                    <SoftGroup software={softWares()}/>
                                    <LocationsContainer locations={locations()}/>
                                    <SliderContainer configurator={configurator()}/>
                                    <OrderCommentContainer/>
                                    <OrderButtonAndPrivacyCheckboxContainer/>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>

            <div className={"lnd__col2 info-mobile configurator-update"}>
                <RightSideContainer data-configurator={configurator()}/>
            </div>
        </>
    )
}


export default App;
