import React from "react";
import {useSelector} from "react-redux";

export const NumberCores = (props) => {
    const numberOfCores = useSelector(state => state.slider.cpu)
    return (
        <tr>
            <td className="label">
                Количество ядер
            </td>
            <td className="value">
                <span className="configurator-item-value__cpu">{numberOfCores}</span></td>
        </tr>
    )
}

