import React from "react";
import ViewTypeContainer from "./elements/ViewTypeContainer";
import AutoButtonLocation from "./elements/AutoButtonLocation";
import CountryButton from "./elements/CountryButton";
const CountryContainer = (props) => {
    const locations = props['data-locations']
    const viewType = props['data-viewType']
    console.log("CountryContainer", locations)
    const getCountryButtons = () => {
        const countryButtons = []
        Object.keys(locations).forEach(key => {
            countryButtons.push(<CountryButton data-country={key}/>)
        })
        return countryButtons
    }
    return (
        <div className="subgroups th">
            <ViewTypeContainer data-view={viewType}/>
       {/*<BreadCrumbsContainer/>*/}
            <div className="select-subgroups select-subgroups-location" data-name="location">
                <AutoButtonLocation/>
                {getCountryButtons()}
            </div>
        </div>
    )
}
export default CountryContainer
