export const getParsedPathConfig = () => {
    //тут берем path и смотрим состав после ? пример (https://justhost.ru/services/vps?software=centos9&location=auto&cpu=4&ram=4096&storage_size=50&netbandmb=200&ipv4_count=1&ipv6_count=1&svc_count=1&payment_months=6&storage=ssd&cpu_options=none&promocode=)
    let path = window.location.search

    const params = new URLSearchParams(path);
    const paramsObject = {};

    for (const [key, value] of params.entries()) {
        paramsObject[key] = value;
    }
    return paramsObject
}
