import React from "react";
import {useDispatch, useSelector} from "react-redux";
import {changeComment} from "../redux-state/reducers/rightSectionSlicer";

const OrderCommentContainer = () => {
    const dispatch = useDispatch()
    const commentState = useSelector(state => state.calculator.comment)
    const getComment = () => {
        if(commentState){
            return commentState
        }
        return ''
    }

    const currentComment = getComment()
    const setComment = (value) => {
        console.log("setComment", commentState)
        const newText = value.target.value
        dispatch(changeComment(newText))
    }
    return(
        <div id="user-comment-wrapper" className="form-component">
            <div className="labels"><label>Комментарий к заказу</label></div>
            <textarea name="user_comment" onChange={setComment} value={currentComment}></textarea></div>
    )
}

export default OrderCommentContainer