import {getParsedPathConfig} from "./commonFunctions";

const getPureOses = (softwareArr) => {
    const oses = softwareArr.filter(os => os.software === null);
    const sortedOses = {}
    oses.forEach((key) => {
        let osKey = key.os.key
        if (sortedOses[osKey]) {
            sortedOses[osKey].push(key)
        } else {
            sortedOses[osKey] = []
            sortedOses[osKey].push(key)
        }
    })
    console.log(sortedOses)
    return sortedOses
}

const getSoftwareOses = (softwareArr) => {
    const oses = softwareArr.filter(os => os.software !== null);
    const sortedOses = {}
    console.log(" SORTED OSES ", sortedOses)
    oses.forEach((key) => {
        let osKey = key.software.key !== undefined ? key.software.key : key.software
        if (sortedOses[osKey]) {
            sortedOses[osKey].push(key)
        } else {
            sortedOses[osKey] = []
            sortedOses[osKey].push(key)
        }
    })
    console.log(sortedOses)
    return sortedOses
}

const getSoftForList = (used, allVersions) => {
    console.log("6668", allVersions)
    return allVersions.filter(os => os.uid !== used.uid)
}

const getListsOfSoftWare = (softwareArr) => {
    const pureOses = getPureOses(softwareArr)
    const softwareOses = getSoftwareOses(softwareArr)
    return {os: pureOses, software: softwareOses}
}

const getDefaultSoftware = (constructorOptions) => {
    return constructorOptions["value"]
}

const getActiveGroupButton = (stateSoftWare, constructorSoftware) => {
    let {software} = getParsedPathConfig()
    if (software !== undefined && software !== null && software !== '') {
        const constructorSoftWare = getSoftwareValueFromOptions(constructorSoftware.options, software)
        if (constructorSoftWare.software !== null) {
            return {osButton: false, softButton: true}
        }
    } else if (stateSoftWare.software !== null) {
        switch (stateSoftWare.software) {
            case "os":
                return {osButton: true, softButton: false}
            case  'software':
                return {osButton: false, softButton: true}
        }
    }

    const constructorSoftWare = getSoftwareValueFromOptions(constructorSoftware.options, constructorSoftware.value)
    if (constructorSoftWare.software !== null) {
        return {osButton: false, softButton: true}
    }
    return {osButton: true, softButton: false}
}

const getSoftwareValueFromOptions = (constructorSoftwareOptions, uid) => {
    return constructorSoftwareOptions.filter(os => os.uid === uid)[0]
}

export {getPureOses, getSoftForList, getDefaultSoftware, getListsOfSoftWare, getActiveGroupButton,}