import React from "react";
import ListNodesCityButton from "./ListNodesCityButton";
import {useDispatch, useSelector} from "react-redux";
import {changeUserCity, changeUserNode, changeNodeListExpand} from "../../../redux-state/reducers/locationSlicer";

const CityButton = (props) => {
    const citiesLocations = props['data-locations']
    const cityCountryString = `${citiesLocations[0].city}, ${citiesLocations[0].country}`
    const cityInLowerCase = citiesLocations[0].city.toLowerCase()
    const countryInLowerCase = citiesLocations[0].country.toLowerCase()
    let defaultNode = citiesLocations[0]
    const arrowFlag = citiesLocations.length >= 2
    const dispatch = useDispatch()
    const currentNodeInExpand = useSelector((state) => state.location.node_list_expand)
    const currentUserNode = useSelector(state => state.location.user_node)
    let activeButton = false
    const setNewUserCityAndNode = (node) =>
    {
        console.log(" onclick setNewUserCityAndNode ")
        const city = node.city
        dispatch(changeUserCity(city))
        dispatch(changeUserNode(node))
    }
    console.log("CityButton ", citiesLocations)
    const getListOfNodes = (node) => {
        const nodes = []
        citiesLocations.forEach((value,key) => {
            if(value.uid !== defaultNode.uid){
                nodes.push(<ListNodesCityButton data-location={value} data-onclick={setNewUserCityAndNode}/>)
            }
        })
        return nodes
    }
    const changeExpandList = () => {
        if(!currentNodeInExpand && citiesLocations.length >= 2) {
            dispatch(changeNodeListExpand(defaultNode))
            setNewUserCityAndNode(defaultNode)
        }else{
            dispatch(changeNodeListExpand(null))
        }
    }

    const checkButtonForExpandList = () => {
        if(currentNodeInExpand){
            if (currentNodeInExpand.city === citiesLocations[0].city && currentNodeInExpand.country === citiesLocations[0].country){
                activeButton = true
                return true
            }
        }
        return false
    }
    const getDefaultNode = () => {
        const currentButtonCity = citiesLocations[0].city
        const currentButtonCountry = citiesLocations[0].country
        if(currentUserNode) {
            if (currentButtonCity === currentUserNode.city && currentButtonCountry === currentUserNode.country) {
                defaultNode = currentUserNode
                activeButton = true
            }
        }
    }
    getDefaultNode()
    return (
        <div className="subgroup-item active" data-subgroup={countryInLowerCase}>
            <div className= {activeButton ? "select-group select-group-city active" : "select-group select-group-city"}>
                <div
                    className={`select-group__content select-group__content-${cityInLowerCase} geo geo-${countryInLowerCase}`}>

                    <div className="select-group__header" onClick={() =>setNewUserCityAndNode(defaultNode)}>
                        {cityCountryString}
                    </div>

                    <div className="select-group__selector" onClick={citiesLocations.length >= 2 ? changeExpandList : () => setNewUserCityAndNode(defaultNode) }>
                        <div className="select-group__selected">{defaultNode.name}</div>
                        <div className={arrowFlag ? checkButtonForExpandList() ? "arrow active" : "arrow" : "arrow disabled"} onClick={changeExpandList}>
                            <div className="arrow-left"></div>
                            <div className="arrow-right"></div>
                        </div>
                        <div className={checkButtonForExpandList() ? "select-group__options active" : "select-group__options"} data-name={cityInLowerCase}>
                            <div className="select-group__options-content">

                                {getListOfNodes()}

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )

}

export default CityButton