import React from "react";
import CityButton from "./elements/CityButton";
import {useDispatch, useSelector} from 'react-redux';
import AutoButtonLocation from "./elements/AutoButtonLocation";
import ViewTypeContainer from "./elements/ViewTypeContainer";
const AllLocationsContainer = (props) => {
    const locations = props['data-locations']
    const viewType = props['data-viewType']
    console.log("Allocations", locations)
    const getCityButtons = () => {
        const cityButtons = []
        Object.keys(locations).forEach((key)=> {
            cityButtons.push(<CityButton data-locations={locations[key]}/>)
        })
        return cityButtons
    }
    return(
        <div className="subgroups list">
        <ViewTypeContainer data-view={viewType}/>
            <div className="select-subgroups select-subgroups-location" data-name="location">
            <AutoButtonLocation/>

            <div className="group" data-key="subgroup_0">
                <div className="select-groups select-groups-location" data-name="location">
                    {getCityButtons()}
                </div>
            </div>
        </div>
        </div>
    )
}

export default AllLocationsContainer