import {createSlice} from "@reduxjs/toolkit";

const initialState = {
    check_box: null,
    comment: null,
    promo: null,
}

export const changeRightSectionReducer = createSlice(
    {
        name: 'RightSectionChanger',
        initialState,
        reducers: {
            changeCheckBox: (state, action) => {
                state.check_box = action.payload
            },
            changeComment: (state, action) => {
                state.comment = action.payload
            },
            changePromo: (state, action) => {
                state.promo = action.payload
            },
        }
    }
)

export const {
    changePromo,
    changeCheckBox,
    changeComment,
} = changeRightSectionReducer.actions
export default changeRightSectionReducer.reducer