import React from "react";
import {useSelector} from "react-redux";
import {getHddTech} from "../../common/rigthSideContainerFunctions";

export const OrderName = (props) => {
    const hddTechIndex = useSelector(state => state.slider.storage)
    const configurator = props['data-configurator']
    const hddTech = getHddTech(hddTechIndex, configurator)

    return (<tr>
        <td colSpan="2" className="order-part-name">
            <h2>
                VPS KVM на <span className="configurator-item-value__storage">{hddTech}</span>
            </h2>
        </td>
    </tr>)
}

