import React from "react";
import SoftList from "./SoftList";
import {useDispatch, useSelector} from "react-redux";
import {changeUserClickSoftListExpand, changeUserOs, changeUserSoftware} from "../../redux-state/reducers/softWareSlicer";

const SoftItem = (props) => {
    const name = props["data-key"]
    const osesVersions = props["data-soft"]
    const softTypeKey = props["data-type"]
    const dispatch = useDispatch()
    const arrowState = useSelector((state) => state.software.user_soft_list_click_expand)
    const listExpand = arrowState === name
    const osInState = useSelector((state) => state.software.user_os)
    const softInState = useSelector((state) => state.software.user_software)
    let isActive = false
    const defaultSoftwareName = () => {
        if (softTypeKey === 'os') {
            if (osInState === null) {
                return osesVersions[0].name
            }
            isActive = name === osInState.os.key
            return name === osInState.os.key ? osInState.name : osesVersions[0].name
        } else {
            if (softInState === null) {
                return osesVersions[0].os.name
            }
            isActive = name === softInState.software.name
            return name === softInState.software.name ? softInState.os.name : osesVersions[0].os.name
        }
    }
    const changeUserSoftwareOrOs = (object) => {

        if (softTypeKey === 'os') {
            dispatch(changeUserOs(object))
        } else {
            dispatch(changeUserSoftware(object))
        }
        dispatch(changeUserClickSoftListExpand(null))
    }
    const changeUserClickSoftListExpandHandler = () => {
        if (osesVersions.length >= 2) {
            dispatch(changeUserClickSoftListExpand(name))
        } else {
            changeUserSoftwareOrOs(osesVersions[0])
        }
    }
    const softNameInSelectGroup = defaultSoftwareName()

    const styleImage = {
        backgroundImage: `url(${osesVersions[0].image})`,
        backgroundRepeat: 'no-repeat',
        backgroundPositionX: 'center',
        backgroundPositionY: '15px',
    }
    return (
        <div className={isActive ? "select-group active" : "select-group "}>
            <div className="select-group__header"  onClick={() => changeUserSoftwareOrOs(osesVersions[0])}
                 style={styleImage}>
                {name}
            </div>
            <div className="select-group__selector">
                <div className="select-group__selected" onClick={changeUserClickSoftListExpandHandler}>
                    {softNameInSelectGroup}
                </div>
                <div
                    className={osesVersions.length < 2 ? 'arrow disabled' : listExpand === false ? 'arrow' : 'arrow active'}>
                    <div className="arrow-left">
                    </div>
                    <div className={"arrow-right"}>
                    </div>
                </div>
                <SoftList data-used-version={osesVersions[0]} data-all-versions={osesVersions} data-type={softTypeKey}
                          data-expand={listExpand}/>
            </div>
        </div>)
}

export default SoftItem